html {
    background: #000;
}

body {
    *{box-sizing: border-box}
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-repeat: no-repeat;
    background-origin: content-box;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    margin: 0;
    padding: 0;
    min-height: 100vh;
}

h1 {
    color: #fff;

    a {
        color: #fff;

        &:hover,
        &:focus,
        &:active {
            color: #fff;
            text-decoration: none;
            border-bottom: 1px dashed magenta;
        }
    }
}

#app {
    margin: 0;
    padding: 0;
    color: #2c3e50;
    color: #fff;
    height: 100%;
    width: 100%;
    position: relative;
}

#nav {
    padding: 30px;
    display: none;

        a {
            font-weight: bold;
            color: #2c3e50;

            &.router-link-exact-active {
                color: #42b983;
            }
    }
}

#main {
    text-align: center;
}

#header {
    width: 500px;
    display: block;
    position: fixed;
    left: 7px;
    top: 0px;
    transform: rotate(90deg);
    transform-origin: bottom left;
    text-align: left;
    text-transform: uppercase;
    z-index: 777;

    -webkit-transition: top left ease;
        -moz-transition: top left ease;
         -o-transition: top left ease;
            transition: top left ease;

    h1,
    &.header-top h1 {
        color: #fff;
        display: inline-block;
        font-size: 2.222rem;
        line-height: normal;
        margin: 0 11px 0 0;
        padding: 0;

        -webkit-transition: font-size .5s;
        -moz-transition: font-size .5s;
         -o-transition: font-size .5s;
            transition: font-size .5s;
    }

    p,
    &.header-top p {
        color: #fff;
        display: inline-block;
        font-size: .888rem;
        line-height: normal;
        margin: 0;
        padding: 0;

        -webkit-transition: font-size .5s;
        -moz-transition: font-size .5s;
         -o-transition: font-size .5s;
            transition: font-size .5s;
    }

    &.header-scroll {
        left: 7px;
        top: 7px;

        -webkit-transition: top left ease;
        -moz-transition: top left ease;
         -o-transition: top left ease;
            transition: top left ease;

        h1 {
            font-size: 1.111rem;
        }
        p {
            font-size: .777rem;
        }
    }
}

@media only screen and (min-width: 963px) {
    #header {
        left: 10px;
        top: 0px;
        

        h1,
        &.header-top h1 {
            font-size: 2.639rem; 
        }

        p,
        &.header-top p {
            font-size: 1.111rem;
        }

        &.header-scroll {
            left: 8px;
            top: 0px;

            h1 {
                font-size: 2.222rem;
            }
            p {
                font-size: 1.111rem;
            }
        }
    }
}

#footer {
    color: #fff;
    display: block;
    font-size: 0.65em;
    position: fixed;
    right: -500px;
    bottom: 15px;
    transform: rotate(-90deg);
    transform-origin: bottom left;
    text-transform: uppercase;
    text-align: left;
    width: 510px;

    span {
        display: inline-block;
        padding: 0 7px;
    }

    a {
        color: #fff;
        text-decoration: none;

        &:hover,
        &:active,
        &:focus {
            color: magenta;
            text-decoration: underline;
        }
    }
}

.pg-content-container {
    //background: blue;
    padding-top: 22px;
    padding-bottom: 22px;
    margin-left: 57px;
    margin-right: 57px;
}

// MyTree SVG
#tree-svg-wrap {
    display: block;
    margin: 111px auto;
    width: 100%;
    overflow: hidden;
    
    img {
        margin-left: -50%;
        width: 200%;
        // box-shadow: 120px 80px 40px 20px #0ff;
    }
}

@media only screen and (min-width: 777px) {
    #tree-svg-wrap {
        margin: 77px auto;

        img {
            margin-left: -15%;
            width: 133%;
        }
    }
}

@media only screen and (min-width: 963px) {
    #tree-svg-wrap {
        margin: 0 auto;
        
        img {
            width: 100%;
            margin-left: 0;
            max-height: 100vh;
        }
    }
}

  
  
  

// SPACE via CodePen
.stars, .twinkling, .clouds {
    position: absolute;
    position:fixed;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
}

.stars {
    z-index: -3;
    background: #000 url("/img/stars/stars.png") repeat top center;
}

.twinkling {
    z-index: -2;
    background: transparent url("/img/stars/twinkling.png") repeat top center;
    animation: move-twink-back 200s linear infinite;
}

.clouds {
    z-index: -1;
    background: transparent url("/img/stars/clouds.png") repeat top center;
    animation: move-clouds-back 200s linear infinite;
}

@keyframes move-twink-back {
    from {
        background-position: 0 0;
    }
    to {
        background-position: -10000px 5000px;
    }
}

@keyframes move-clouds-back {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 10000px 0;
    }
}


#moon {
    display: inline-block;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    box-shadow: 15px 15px 0 0 #fff;
    position: absolute;
    top: 3%;
    right: 10%;
    z-index: -1;
}

#orb {
    width: 1px;
    height: 1px;
    border-radius: 50%;
    background-color: #fff;
    box-shadow:
    0 0 60px 30px #fff,  /* inner white */
    0 0 100px 60px #f0f, /* middle magenta */
    0 0 140px 90px #0ff; /* outer cyan */
    position: absolute;
    top: 9%;
    right: 11%;
    opacity: 0.4;
    filter: alpha(opacity=40);
    z-index: -1;
}